import { reactive, toRefs } from 'vue';

export default function() {
	let state = reactive({
		reviews: [],
		isLoading: false,
		isAnimated: true,
		statusCode: null,
	});

	function readApi(url) {
		state.isLoading = true;

		return fetch(url)
					.then(response => {
						state.statusCode = response.status;
						return response.json();
					})
					.then(data => {
						state.reviews = data;

						setTimeout(() => {
							state.isLoading = false;
						}, 2000);
						state.isAnimated = false;
					})
	}

	return {
		...toRefs(state),
		readApi,
	}
}